/* home page */
* {
  margin: 0;
  padding: 0;
}
hero_text p {
  font-size: 90%;
}

.percent_ {
  text-align: center;
  width: 100%;
  padding-bottom: 5px;
  border-bottom: 5px solid #fd6621;
  background-color: black;
  padding-top: 10px;
  margin-top: 10px;
  border-top: 5px solid #fd6621;
}
.logo_rsb {
  position: absolute;
  left: 30px;
  bottom: -13px;
  height: 300px;
  width: 350px;
  border-radius: 80px 10px 80px 10px;
}

section h1 {
  margin: 0%;
}

/* this line corresponds to footer  */
* {
  box-sizing: border-box;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}
/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 1114px) {
  .column {
    width: 100% !important;
  }
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.RSB_para {
  font-family: sans-serif;
  font-size: 25px;
  padding: 5%;
  margin: 4%;
  margin-left: 10%;
  margin-right: 10%;
  text-align: center;
}
.row {
  border-radius: 25px;
  background: black;
  background-position: left top;
  background-repeat: repeat;
  z-index: 3;
  margin-top: 5%;
  margin-bottom: 5%;
  padding-left: 5%;
  padding-bottom: 1%;
  padding-top: 3%;
}

.deck_para {
  font-family: sans-serif;
  font-size: 25px;
  margin-left: 10%;
  margin-right: 10%;
  text-align: center;
}

.RSB {
  position: relative;
  bottom: -25px;
  text-align: center;
  justify-content: center;
  color: white;
  font-family: Garamond;
}

.Decks {
  margin: 0;
  text-align: center;
}

.hero {
  width: 100%;
  margin-bottom: 0;
}
html {
  font-family: Garamond;
  font-size: 18px;
  background-color: #242f3b;
  color: white;
}
footer p {
  position: relative;
  bottom: -105px;
  right: 1%;
  z-index: 1;
}
footer a {
  position: relative;
  top: 90px;
  right: -40px;
  z-index: 1;
  font-size: 50px;
  padding: 8px;
}
footer {
  bottom: 100%;
  text-align: center;
  font-size: 20px;
  margin-top: 50px;
  margin-bottom: -1px;
  /* padding-top:none; */
  background-color: black;
}

.rsb_img {
  position: relative;
  width: 200px;
  right: 7%;
  top: -80px;
}
.rail {
  width: 100%;
  padding-right: 5px;
  width: 100%;
}
.hero__wrap {
  background-image: url("/img/halfpipe.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  /* position: relative;
      margin-bottom: 250px; */
}
.header-content {
  position: absolute;
  top: 80%;
  left: 60%;
  color: #000;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  /* text-shadow: -2px -2px 0 #000; */
  text-align: center;
  text-shadow: 2px 2px 1px #fd6621;
  font-size: 2em;
}
@media (max-width: 980px) {
  .logo_rsb {
    display: none;
  }

  .header-content {
    top: 50%;
    left: 50%;
  }
}
@media (max-width: 450px) {
  .header-content {
    font-size: 30px;
  }
}
@media (max-width: 500px) {
  .hero__wrap {
    background-position: -50px;
  }
}

/* shared css */
.nav_img {
    height: 40px;
    width: 100px;
    object-fit: cover;
    padding-top: 0%;
    float: right;
  }
  
  nav {
    width: 100%;
    background: black;
    box-shadow: 0 5px 5px #fd6621;
    position: fixed;
    top: 0;
    z-index: 1;
  }
  
  nav a {
    font-size: 3vw;
    margin-right: 10px;
    text-decoration: none;
    color: #fdfdfd;
    padding: 0px 20px;
  }
  
  nav a:hover {
    background: #fd6721;
    color: black;
  }
  footer {
    bottom: 100%;
    text-align: center;
    font-size: 20px;
    margin-bottom: 5%;
    padding-top: 5%;
    border-top: solid #fd6721;
  }
/* checkout */
* {
    margin: 0;
    padding: 0;
  }
  html {
    background-color: #242f3b;
  }
  body {
    font-family: Arial;
    font-size: 17px;
    padding: 8px;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .rowCheckout {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px;
    margin-top: 5%;
  }
  
  .col-25 {
    flex: 25% 1;
  }
  
  .col-50 {
    flex: 50% 1;
  }
  
  .col-75 {
    flex: 75% 1;
  }
  
  .col-25,
  .col-50,
  .col-75 {
    padding: 0 16px;
  }
  
  .container {
    background-color: #fff;
    padding: 5px 20px 15px 20px;
    border: 1px solid #fff;
    border-radius: 3px;
  }
  
  input[type="text"] {
    width: 100%;
    margin-bottom: 20px;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  label {
    margin-bottom: 10px;
    display: block;
    color: #000;
  }
  
  .icon-container {
    margin-bottom: 20px;
    padding: 7px 0;
    font-size: 24px;
  }
  
  .btn {
    background-color: #04aa6d;
    color: white;
    padding: 12px;
    margin: 10px 0;
    border: none;
    width: 100%;
    border-radius: 3px;
    cursor: pointer;
    font-size: 17px;
  }
  
  .btn:hover {
    background-color: #45a049;
  }
  
  a {
    color: #2196f3;
  }
  
  hr {
    border: 1px solid lightgrey;
  }
  
  span.price {
    float: right;
    color: grey;
  }
  
  @media (max-width: 800px) {
    .rowCheckout {
      flex-direction: column-reverse;
    }
    .col-25 {
      margin-bottom: 20px;
    }
  }
  
.hero img {
    width: 100%;
    height: 219px;
  }
  h2 {
    color: #000;
  }
  .hero {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    grid-gap: 0rem;
    background-color: #fff;
    padding-top: 40px;
    margin-bottom: 25px;
    border-bottom: #000 solid;
    background-color: #000;
    border: solid #fd6721;
  }
  .hero h1 {
    text-align: center;
    color: #fff;
  }
  .hero h1:hover {
    background: #fd6721;
    color: #000;
  }
  
  .decks img {
    width: 240px;
    height: 240px;
    box-shadow: 10px 10px 20px #fd6721;
  }
  
  .decks {
    display: grid;
  
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    grid-gap: 10px;
    background-color: #fff;
    padding-bottom: 70px;
    padding-top: 50px;
    border: solid #fd6721;
  }
  
  .price {
    text-align: center;
  }
  .items-descrption img {
    width: 50%;
  }
  
  @media (max-width: 1000px) {
    .decks {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media (max-width: 509px) {
    .decks {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
  html {
    background-color: #242f3b;
  }
  
  .price:hover {
    background: #fd6721;
    color: #000;
  }
  .price {
    color: #fff;
  }
  
  /* search bar css */
  .searchBtn {
    width: 95%;
    margin: 0 auto;
    padding-top: 50px;
    text-align: center;
    padding-bottom: 10px;
  }
  
  #search-container {
    margin: 1em 0;
  }
  
  #search-container input {
    background-color: transparent;
    width: 40%;
    border-bottom: 2px solid #fd6721;
    padding: 1em 0.3em;
    border-radius: 5px;
  }
  
  #search-container input:focus {
    border-bottom-color: #fd6621;
  }
  
  #search-container button {
    padding: 1em 2em;
    margin-left: 1em;
    background-color: #fd6721;
    color: #ffffff;
    border-radius: 10%;
  
    margin-top: 0.5em;
  }
  
  .button-value {
    position: relative;
    left: -60px;
    border: 2px solid #fd6721;
    padding: 1em 2.2em;
    border-radius: 3em;
    margin: 20px;
    background-color: transparent;
    color: #ffffff;
    cursor: pointer;
  }
  
  .active {
    background-color: #fd6721;
    color: #fd6621;
  }
  
