/* home page */
* {
  margin: 0;
  padding: 0;
}
hero_text p {
  font-size: 90%;
}

.percent_ {
  text-align: center;
  width: 100%;
  padding-bottom: 5px;
  border-bottom: 5px solid #fd6621;
  background-color: black;
  padding-top: 10px;
  margin-top: 10px;
  border-top: 5px solid #fd6621;
}
.logo_rsb {
  position: absolute;
  left: 30px;
  bottom: -13px;
  height: 300px;
  width: 350px;
  border-radius: 80px 10px 80px 10px;
}

section h1 {
  margin: 0%;
}

/* this line corresponds to footer  */
* {
  box-sizing: border-box;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}
/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 1114px) {
  .column {
    width: 100% !important;
  }
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.RSB_para {
  font-family: sans-serif;
  font-size: 25px;
  padding: 5%;
  margin: 4%;
  margin-left: 10%;
  margin-right: 10%;
  text-align: center;
}
.row {
  border-radius: 25px;
  background: black;
  background-position: left top;
  background-repeat: repeat;
  z-index: 3;
  margin-top: 5%;
  margin-bottom: 5%;
  padding-left: 5%;
  padding-bottom: 1%;
  padding-top: 3%;
}

.deck_para {
  font-family: sans-serif;
  font-size: 25px;
  margin-left: 10%;
  margin-right: 10%;
  text-align: center;
}

.RSB {
  position: relative;
  bottom: -25px;
  text-align: center;
  justify-content: center;
  color: white;
  font-family: Garamond;
}

.Decks {
  margin: 0;
  text-align: center;
}

.hero {
  width: 100%;
  margin-bottom: 0;
}
html {
  font-family: Garamond;
  font-size: 18px;
  background-color: #242f3b;
  color: white;
}
footer p {
  position: relative;
  bottom: -105px;
  right: 1%;
  z-index: 1;
}
footer a {
  position: relative;
  top: 90px;
  right: -40px;
  z-index: 1;
  font-size: 50px;
  padding: 8px;
}
footer {
  bottom: 100%;
  text-align: center;
  font-size: 20px;
  margin-top: 50px;
  margin-bottom: -1px;
  /* padding-top:none; */
  background-color: black;
}

.rsb_img {
  position: relative;
  width: 200px;
  right: 7%;
  top: -80px;
}
.rail {
  width: 100%;
  padding-right: 5px;
  width: 100%;
}
.hero__wrap {
  background-image: url("/img/halfpipe.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  /* position: relative;
      margin-bottom: 250px; */
}
.header-content {
  position: absolute;
  top: 80%;
  left: 60%;
  color: #000;
  transform: translate(-50%, -50%);
  /* text-shadow: -2px -2px 0 #000; */
  text-align: center;
  text-shadow: 2px 2px 1px #fd6621;
  font-size: 2em;
}
@media (max-width: 980px) {
  .logo_rsb {
    display: none;
  }

  .header-content {
    top: 50%;
    left: 50%;
  }
}
@media (max-width: 450px) {
  .header-content {
    font-size: 30px;
  }
}
@media (max-width: 500px) {
  .hero__wrap {
    background-position: -50px;
  }
}
